@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@100;300;400;700&display=swap);
:root {
    --color-primary1: hsl(233, 26%, 24%);
    --color-primary2: hsl(136, 65%, 51%);
    --color-primary3: hsl(192, 70%, 51%);

    --color-neutral1: hsl(233, 8%, 62%);
    --color-neutral2: hsl(220, 16%, 96%);
    --color-neutral3: hsl(0, 0%, 98%);
    --color-neutral4: hsl(0, 0%, 100%);

    --font-size-large: 1.2rem;
    --font-size-small:calc(var(--font-size-large) * 0.63);
    --font-size-smaller:calc(var(--font-size-large) * 0.51);

    --mobile-width: 376px;

    --space: 1.5rem;
}

* {
    text-decoration: none;
    outline: none;
    border: none;
    list-style: none;
    margin: 0;
    padding: 0;
    background-color: inherit;
    font-size: 1.2rem;
    font-size: var(--font-size-large);
    color: inherit;
    text-align: center;
    font-family: 'Public Sans', sans-serif;
    font-weight: 300;
}

h1 {
    font-size: 155%;
}

p {
    font-size: 80%;
}

*, *:before, *:after {
    box-sizing: border-box;
}

.box {
    --box-space: var(--space);
    padding: var(--box-space);
}

.outer__margin {
    --outer-margin-space: var(--space);
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

.stack {
    --stack-space: 1rem;
}

.stack > * + * {
    margin-top: var(--stack-space);
}

.queue {
    --queue-space: 1rem;
}

.queue > * + * {
    margin-left: var(--queue-space);
}

.switcher {
    display: flex;
    flex-wrap: row;
}

.switcher > * {
    flex-grow: 1;
    flex-basis: 0;
}

.grid {
    --gird-space: 1rem;
    --minimum: 20ch;
    display: grid;
    grid-gap: var(--gird-space);
    gap: var(--gird-space);
}

@supports (width: min(var(--minimum, 100%))) {
    .grid {
        grid-template-columns: repeat(auto-fit, minmax(min(var(--minimum), 100%), 1fr));
    }
}

.center {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (min-width: var(--mobile-width)) {
    .box {}
}
header {
    background-color: var(--color-neutral4);
    display: flex;
    position: fixed;
    align-items: center;
    z-index: 1000;
    width: 100vw;
}

header > :first-child {
    margin-right: auto;
}

header > :last-child {
    margin-left: auto;
}

nav {
    display: none;
}

nav:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-neutral1);
    opacity: 0.6;
}

nav > :first-child {
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    right: 1.5rem;
    background-color: var(--color-neutral4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.showNav {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    top: 4.4rem;
    width: 100%;
    height: 100vh;
    background-color: transparent;
}

.intro > :first-child {
    position: relative;
    text-align: center;
    background: url(/FEM-Easybank/static/media/bg-intro-mobile.c23dd1d0.svg);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
}

.intro > :first-child> :only-child {
    position: relative;
    max-width: 85%;
    top: -1.5rem;
    bottom: 0;
}

.intro > :nth-child(2) {
    color: var(--color-primary1);
}

.intro p {
    text-align: justify;
} 

.request__invite {
    background-color: var(--color-primary2);
    border-radius: 3rem;
    width: 10rem;
    padding: 1rem;
    font-size: var(--font-size-small);
    font-weight: 400;
    color: var(--color-neutral4);
}

.why__us {
    color: var(--color-primary1);
    background-color: var(--color-neutral2);
}

.latest__article * :not(h1) {
    background-color: var(--color-neutral4);
    font-size: var(--font-size-smaller);
}

.latest__article h2 {
    font-size: var(--font-size-small);
}

.latest__article > :first-child {
    margin-top: calc(var(--space) * 3);
}

.latest__article > :last-child * {
    text-align: left;
    background-color: var(--color-neutral3);
}

.card > img {
    max-width: 100%;
}

footer {
    background-color: var(--color-primary1);
    color: var(--color-neutral2);
}

footer svg :nth-child(2) :first-child {  /*I don't know much about svg, so this is kinda crappy*/
    fill: white;
}

footer p {
    font-size: var(--font-size-small);
}
